.project{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid white;
    border-radius: 10px 10px 0px 0px;   
    overflow: hidden;
} 
.pBrowser{
    height: 20px;
    background-color: rgb(223, 221, 221);
    display: flex;
    align-items: center;
    position: sticky;
}
.pCircle{
     width: 6px;
     height: 6px;
     border-radius: 50%;
     margin: 3px;
     background-color: white;
}

.pImg{
    width: 100%;
}
